@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300&display=swap");

.App {
  text-align: center;
  height: 100vh;
  color: white;
  background-size: cover;
  background-attachment: fixed;
}
.Weather {
  display: flex;
  color: white;
  justify-content: center;
}
.Weather .City {
  position: absolute;

  bottom: 5vh;
  left: 5vw;
  text-align: left;
  margin: 0;
}

.Weather h1 {
  margin: 0;
  font-size: 96px;
  font-family: "Roboto", sans-serif;
}

.Weather h2 {
  margin: 0;
  font-family: Roboto;
  font-style: italic;
  font-weight: normal;
  font-size: 24px;
}

.format {
  /* background-color: white; */
  justify-content: center;
  align-items: center;
  opacity: 70%;

  display: flex;
}

.InputForm {
  /* margin-top: 5vh; */
  padding-top: 5vh;
}

.form input {
  border-radius: 10px;
  border: none;
  height: 4vh;
}

.Quote {
  padding-top: 10vh;
}
